/* Basic styling */
.app {
    display: flex;
    flex-direction: column;
    min-height: auto;
    background: linear-gradient(150deg, #ffecd2 0, #fcb69f);
}

.app-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    min-height: 88vh !important;
}

.footer {
    position: auto !important;
    bottom: 0;
    width: 100%;
    height: 0.5rem !important;
    background-color: transparent !important;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Header and heading */
.header {
    background-color: transparent !important;
    opacity: 20 !important;
    min-height: 10vh !important;
}

.heading {
    margin-bottom: 2rem !important;
    text-align: center;
    font-weight: bold !important;
    color: #302f2c;
}

/* Input field */
.my-input-field {
    border: 1px solid #ccc;
    border-radius: 4px !important;
    padding: 6px;
    font-size: 16px;
    font-weight: bold;
    width: 20rem;
    margin-right: 20rem;
    margin-bottom: 10px;
    opacity: 80%;
    background-color: rgba(255, 255, 255, 0.662) ;
}

/* Buttons */
.button-container {
    display: inline-block;
    margin-left: 0rem !important;
    padding: 4px 8px !important;
    font-size: 12px !important;
}

.button-deselected {
    border: 1px solid #967a6b !important;
    color: #1d1714 !important;
}

.button-selected {
    color: #ffffff !important;
    background-color: #caa490 !important;
}

/* Summary and clipboard */
.prompt-title {
    font-weight: bold !important;
    font-size: 28px !important;
    margin-top: 3rem !important;
}

.summary-options-container {
    padding: 0.5rem;
    font-weight: bold !important;
}

.btn-clipboard {
    margin-top: 1rem !important;
    padding: 8px 24px !important;
    border: 1px solid #f2ccac !important;
    color: white !important;
    opacity: 80%;
    font-size: 16px !important;
    background: linear-gradient(60deg, #a08670, #b4957d, #7e6a58);
    background-size: 400% 400%;
    animation: animatedgradient 6s ease infinite;
}

/* Titles and rows */
.row-title {
    padding-left: 8px;
    margin-top: 0.5rem !important;
}

.row-container {
    margin: 1rem;
    margin-left: 0rem !important;
}

.summary-container {
    margin: 1rem;
    margin-left: 0rem !important;
}

/* Text */
.h4 {}

.h5 {}

.h6 {}

/* Gradient animation */
@keyframes animatedgradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
